export default {
  middleware: "auth",
  auth: "guest",
  message: "",

  data() {
    return {
      errMsg: null,
      loginData: {
        email: "",
        password: "",
      },
    };
  },
  methods: {
    async login() {
      try {
        let response = await this.$auth.loginWith("local", {
          data: this.loginData,
        });
        this.$router.push("/");
      } catch (err) {
      
        this.errMsg = true;
      }
    },
  },
  created() {
    this.message = this.$route.query.msg;
    this.loginData.email = this.$route.query.email;
  },
};
